@font-face {
    font-family: "Obviously Black";
    src: url("../fonts/Obviously-Black.otf") format("opentype");
  }
$size-tiny: 100px;
$size-normal: 300px;
$size-large: 500px;

// Usage example
.ws_size-normal {
  width: $size-normal;
}
  body {
    font-family: "Obviously Black", sans-serif;
  }
  
  .hero-test {
    height: 300px;
    background-image: url("../images/sass-test.jpg");
    background-size: cover;
    background-position: center;
    margin: 2rem;
    border-radius: 12px;
  }






  .ws_motion {
    position: relative;
    width: 100%;
    aspect-ratio: 1 / 1; // Maintain square base
    max-width: 600px;
    margin: 0 auto;
    text-align: center;
  }
  
  .ws_motion > div {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none; // Optional: make them ignore mouse
  }
  
  .ws_size-normal img {
    width: 100%;
    max-width: 200px;
  }
  
  // Z-INDEX layering
  #cloud { z-index: 1; }
  #planet { z-index: 2; }
  #dotes { z-index: 3; }
  #satellite { z-index: 4; }
  #doubled_cloud { z-index: 5; }
  
  // You can optionally add different sizes
  $ws-sizes: (
    tiny: 80px,
    normal: 160px,
    large: 240px
  );
  
  @each $name, $size in $ws-sizes {
    .ws_size-#{$name} img {
      max-width: $size;
    }
  }

  